'use client'; // เพิ่มบรรทัดนี้เพื่อบอกว่าเป็น Client Component

import { usePathname } from 'next/navigation'; // ใช้ usePathname เพื่อดึง URL ปัจจุบัน
import Link from 'next/link';

export default function Navbar() {
  const pathname = usePathname(); // ดึง path ปัจจุบันจาก usePathname

  // ฟังก์ชันตรวจสอบว่าเมนูใดตรงกับ URL ปัจจุบัน
  const isActiveLink = (link) => {
    return pathname === link
      ? 'bg-blue-600 px-4 py-2 rounded-md hover:bg-blue-500 text-gray-400' 
      : 'hover:text-gray-400';
  };

  return (
    <nav className="bg-gray-800 text-white p-4 shadow-md">
      <div className="max-w-7xl mx-auto flex justify-between items-center space-x-6">
        <div className="font-bold text-xl">MicroFinance</div>

        {/* เมนูและปุ่ม Register */}
        <div className="flex items-center space-x-4">
          {/* เมนู Home */}
          <Link href="/">
            <span className={`${isActiveLink('/')} cursor-pointer`}>Home</span>
          </Link>


          {/* เมนู Blog */}
          <Link href="./blog">
            <span className={`${isActiveLink('/blog')} cursor-pointer`}>Blog</span>
          </Link>

          {/* เมนู Courses */}
          <Link href="/courses">
            <span className={`${isActiveLink('/courses')} cursor-pointer`}>Donate us</span>
          </Link>

          {/* เมนู About */}
          <Link href="/about">
            <span className={`${isActiveLink('/about')} cursor-pointer`}>About</span>
          </Link>

          {/* ปุ่ม Register Now */}
          <button className="bg-blue-600 text-white px-6 py-2 rounded-md hover:bg-blue-500">
            Login
          </button>
        </div>
      </div>
    </nav>
  );
}
